.section {
  max-width: 100vw;
  padding: 30px 5%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px;
}

.card {
  margin: auto;
  display: flex;
  flex-direction: row;
  -webkit-box-shadow: 24px 13px 35px -7px rgba(0, 0, 0, 1);
  -moz-box-shadow: 24px 13px 35px -7px rgba(0, 0, 0, 1);
  box-shadow: 24px 13px 35px -7px rgba(0, 0, 0, 1);
  border-radius: 10px;
  width: 90%;
  height: 350px;
}

.cardLeft {
  width: 35%;
}

.cardLeft > img {
  width: 100%;
  height: 100%;
  border-radius: 10px 0 0 10px;
}

.cardRight {
  padding: 10px 20px;
  width: 65%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.cardRight > span {
  display: flex;
  align-items: center;
  width: 100%;
}

.cardTitle,
.cardDate {
  height: 10%;
}

.cardTitle {
  font-weight: bolder;
  font-size: 19px;
}

.cardDate {
  font-size: 15px;
  color: gray;
}

.cardOverview {
  white-space: nowrap;
  line-height: 1.4;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
  height: 120px;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  font-size: 14px;
  font-weight: 500;
}

@media screen and (max-width: 1024px) {
  .section {
    padding: 30px 0;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 50px;
  }

  .card {
    width: 70%;
  }

  .cardLeft {
    width: 45%;
  }

  .cardRight {
    width: 55%;
    display: flex;
    flex-direction: column;
  }

  .cardTitle {
    font-size: 21px;
  }

  .cardDate {
    font-size: 16px;
  }
  .cardOverview {
    height: 150px;
    -webkit-line-clamp: 7;
    font-size: 15px;
  }
}

@media screen and (max-width: 768px) {
  .section {
    padding: 30px 0;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 50px;
  }

  .card {
    flex-direction: column;
    width: 80%;
    height: 600px;
  }

  .cardLeft {
    padding-top: 10px;
    width: 100%;
    height: 45%;
    display: flex;
    justify-content: center;
  }

  .cardLeft > img {
    max-width: 350px;
    border-radius: 0;
  }

  .cardRight {
    width: 100%;
    height: 55%;
  }

  .cardTitle {
    font-size: 23px;
  }

  .cardDate {
    font-size: 17px;
  }

  .cardOverview {
    line-height: 2.2;
    -webkit-line-clamp: 7;
    width: 90%;
    height: 200px;
    font-size: 13px;
  }
}
